.imagen-principal{
    overflow: hidden;
    @include tablet{
        img{
            margin-left: 61.7rem;
        }
    }
}
.imagen-contacto {
    @include tablet{
    height: 100rem;
    width: 150rem;
    }

}
@include tablet{
    .formulario-container {
    
        position: absolute;
        top: 20rem;
        left: 10rem;
        width: 40%;
        height: 65%;
        padding: 2rem;
        box-sizing: border-box;
        background-color: $azul; 
        border-radius: 10px; 
        
        p{
            color: $blanco;
            margin-left: 2rem;
            margin-right: 2rem;
        }
        form{
            border-radius: 10px;
            padding: 20px;
            color: $blanco;
            font-weight: bold;
            }
            h2{
                font-family: $fuentesDetalles;
                font-weight: bold;
                color: $blanco;
            }
    }
    .contenido-contacto{
        @include grid(2,2rem);
        margin-left: 2rem;
        margin-right: 2rem;
    }
    .formulario-container {
        position: absolute;
        top: 20rem;
        left: 10rem;
        max-width: 50%;
        padding: 2rem;
        box-sizing: border-box;
        background-color: $azul;
        border-radius: 10px;
    }
    
    .form-item {
        margin-bottom: 1rem; 
    }
    
    .form-item label {
        display: block; 
        margin-bottom: 0.5rem; 
    }
    
    .form-item input,
    .form-item textarea {
        width: 100%;
        padding: 0.5rem;
        border-radius: 2rem;
        border: 1px solid #ccc;
    }
    .text-mensaje{
        grid-row: 3/3;
        grid-column: 3/1;
    }
    .boton-mas-enviar{
        margin-top: 0;
        background-color: $lila;
        color: $azul;
        &:hover{
            background-color: $rosa;
        }
    }
}
.formulario-container{
    background-color: $azul;
    border-radius: 10px;
    p{
        color: $blanco;
        margin-left: 2rem;
        margin-right: 2rem;
    }
    form{
        border-radius: 10px;
        padding: 20px;
        color: $blanco;
        font-weight: bold;
        }
        h2{
            font-family: $fuentesDetalles;
            font-weight: bold;
            color: $blanco;
        }
    .form-item{
        input{
            border-radius: 10px;
        }
        textarea{
            border-radius: 10px;
        }
    }
    .contenido-contacto{
        @include grid(2,2rem);
        
        margin-right: 2rem;
        
    }
    .boton-mas-enviar{
        margin-left: 1rem;
        margin-bottom: 1rem;
        margin-top: 0;
        background-color: $lila;
        color: $azul;
        &:hover{
            background-color: $rosa;
        }
    }
    .form-item textarea {
        width: 100%;
        padding: 0.5rem;
        border-radius: 2rem;
        border: 1px solid #ccc;
    }
    .text-mensaje{
        grid-row: 3/3;
        grid-column: 3/1;
    }
        
}
