.fotos {
    position: relative;
    max-height: 70rem;
    overflow: hidden;

    .slide {
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.5); 
    }

    img {
        width: 100%;
        display: block;
    }
}
@include tablet{
    .contenido-foto {
        opacity: 1; 
        h2 {
            display: flex;
            justify-content: left;
            color: $blanco; // Color del texto
            text-transform: uppercase;
            font-weight: bold;
            font-size: 5px;
        }
    
        .titulo {
            font-weight: normal;
            font-size: 4px;
    
            p {
                margin: 0;
                font-size: 5px;
            }
        }
    
        p {
            padding-top: 0;
            display: flex;
            justify-content: left;
            text-transform: uppercase;
        }
    }
    
}

.contenido-foto {
    opacity: 1; 
    h2 {
        display: flex;
        justify-content: left;
        color: $blanco; // Color del texto
        text-transform: uppercase;
        font-weight: bold;
        font-size: 20px;
    }

    .titulo {
        font-weight: normal;
        font-size: 30px;

        p {
            margin: 0;
            font-size: 20px;
        }
    }

    p {
        padding-top: 0;
        display: flex;
        justify-content: left;
        text-transform: uppercase;
    }
}
@include tablet{
    .contenido-foto{
        .titulo {
            font-weight: normal;
            font-size: 30px;
        
            p {
                margin: 0;
                font-size: 50px;
            }
        }
        
    }
}





