@include tablet{
    .contenedor-casas{
        overflow: auto;
        h3{
            font-size: 1.5rem;
            font-weight: bold;
            
        }
        @include grid(4,2rem);
        
    }
}

@include tablet{
    .contenido-casa{
        display: flex;
        flex-direction: column;
        }
    }
    h3{
            margin: 0;
            color: $rosa;
    }
        
    .casas{
        width: 100%;
        position: relative;
        background-color: $lila;
        padding: 2rem;
        margin-bottom: 2rem;
        border-radius: 2rem;
        .contenido-casa{
            
            ul{
                justify-content: center;
            }
        }
    
    }



.boton-tipo{
    @include tablet{
        display: flex;
        width: 85%;
        
    }
    position: absolute;
    width: 40%;
    margin: 0.2rem;
    margin-top: 0.6rem;
    padding: 0.1em 0.1rem;
    
    
}
@include tablet {
    .boton-mas {
        display: flex;
        width: 70%;
        justify-content: center;
        margin: 0 auto; // Centrar horizontalmente el botón
        &:hover {
            background-color: darken($rosa, 5);
        }
    }
}

.titulo-precio{
    color: $casa_sub;
    text-align: center;
    font-size: 2rem;
    border-top: 4px solid $gris; 
    margin-bottom: 0;
}
.precio{
    color: $grisOscuro;
    font-weight: bold;
    font-size: 2.5rem;
    text-align: center;
    margin-top: 0;

}
.seccion-contenedor{
    display: block;
    @include desktop{
        display: flex;
    flex-direction: column;
    align-items: center;
        
    }
    h1{
        font: $fuentesDetalles;
        font-weight: bold;
        color: $rosa;
    }
    
}
.marco{
    @include tablet{
        width: 136rem;
        margin-bottom: auto;
    }
    background-color: $blanco;
    border-radius: 3rem;
    padding: 2rem;
    display: flex;
    margin-bottom: 2rem;
    flex-wrap: wrap;
    gap: 2rem;
    overflow: auto;
    @include tablet{
        max-width: 100%;
        
    }
}

.iconos-caracteristicas {
    
    display: flex;
    list-style: none;
    padding: 0;
    align-items: center;

    
    li {
        display: flex;
        flex-direction: column; 
        
        padding: 1.2rem;
    }
    p {
        display: flex;
        justify-content: center;
        font-size: 1.4rem;
        font-weight: bold;
        margin: 0;
        color: $azul;
    }
    img {
        width:6.75rem; 
        height: 6rem; 
        
    }
}
